body {
    overflow-y: scroll;
}
.App {
    font-size: 16px;
}
.App * {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

hr,
fieldset {
    border-color: #ccc;
    border-style: solid;
    border-width: 1px;
    margin-bottom: 10px;
}
hr {
    border-width: 0 0 1px 0;
}
